import React, { Component, Fragment } from 'react'
import { Link } from 'react-router-dom'
import { HashLink } from 'react-router-hash-link'

import './app.css'

export class LandingPage extends Component {
	render() {
		return (
			<Fragment>
				<nav id="mainNavbar" className="navbar navbar-dark navbar-expand-md py-0 fixed-top">
					<HashLink to="#top" className="navbar-brand">
						<i className="fas fa-user-tie fa-lg mt-2 mt-md-0" />
					</HashLink>
					<button
						className="navbar-toggler"
						data-toggle="collapse"
						data-target="#navLinks"
						aria-label="Toggle Navigation"
					>
						<span className="navbar-toggler-icon" />
					</button>
					<div className="collapse navbar-collapse" id="navLinks">
						<ul className="navbar-nav">
							<li className="nav-item">
								<HashLink to="#skills" className="nav-link">
									SKILLS
								</HashLink>
							</li>
							<li className="nav-item">
								<HashLink to="#projects" className="nav-link">
									PROJECTS
								</HashLink>
							</li>
							<li className="nav-item">
								<HashLink to="#achievements" className="nav-link">
									ACHIEVEMENTS
								</HashLink>
							</li>
						</ul>
					</div>
				</nav>

				<section id="top" className="container-fluid px-0">
					<div className="row  align-items-center mx-0">
						<div className="col-lg-6 px-0">
							<div id="headingGroup" className="text-white text-center d-none d-lg-block mt-5">
								<h1 className="text-danger">Subhajit Nandi</h1>
								<h1>I build things for the web.</h1>
							</div>
						</div>
						<div className="col-lg-6 px-0">
							<img
								className="img-fluid"
								src="imgs/joshua-aragon-FGXqbqbGt5o-unsplash.jpg"
								alt="laptop on a table"
							/>
						</div>
					</div>
				</section>

				<section className="container-fluid px-0">
					<div className="row align-items-center mx-0 content">
						<div className="col-md-6 px-0 order-2 order-md-1">
							<img
								className="img-fluid"
								src="imgs/lum3n--RBuQ2PK_L8-unsplash.jpg"
								alt="white keyboard, mouse, headphones and notebook"
							/>
						</div>
						<div className="col-md-6 text-center px-0 order-1 order-md-2">
							<div className="row justify-content-center mx-0">
								<div className="col-10 col-lg-8 px-0 blurb mb-5 mb-md-0">
									<h2>ABOUT ME</h2>
									<i className="fab fa-angellist fa-4x text-white my-3 my-md-4" />
									<p className="lead">
										Hello! I'm Subhajit, a web developer based in Jaipur, India who enjoys building
										things that live on the internet. I develop exceptional websites and web apps
										that provide intuitive, pixel-perfect user interfaces with efficient and modern
										backends.
									</p>
									<p className="lead">
										I am currently pursuing my Bachelor's Degree in Computer Science from The LNM
										Institute of Information Technology.
									</p>
								</div>
							</div>
						</div>
					</div>
					<div id="skills" className="row align-items-center mx-0 content">
						<div className="col-md-6 text-center px-0">
							<div className="row justify-content-center mx-0">
								<div className="col-10 col-lg-8 px-0 blurb mb-5 mb-md-0">
									<h2>SKILLS</h2>
									<i className="fas fa-tools fa-4x text-white my-3 my-md-4" />
									<p className="lead">
										I have been working with the MERN stack for the past two years. So, I know the
										ins and outs of the javascript language and have become proficient in solving
										the common bugs.
									</p>
									<p className="lead">
										Few technologies I've been working with recently are TypeScript, GraphQL,
										Material UI and Progressive Web Apps.
									</p>
								</div>
							</div>
						</div>
						<div className="col-md-6 px-0">
							<img
								className="img-fluid"
								src="imgs/kaleidico-26MJGnCM0Wc-unsplash.jpg"
								alt="white board with drawings of the structure of an application"
							/>
						</div>
					</div>
					<div id="projects" className="row align-items-center mx-0 content">
						<div className="col-md-6 px-0 order-2 order-md-1">
							<img className="img-fluid" src="imgs/hand2.png" alt="gloved hand holding a donut" />
						</div>
						<div className="col-md-6 text-center px-0 order-1 order-md-2">
							<div className="row justify-content-center mx-0">
								<div className="col-10 col-lg-8 px-0 blurb mb-5 mb-md-0">
									<h2>PROJECTS</h2>
									<i className="fas fa-cubes fa-4x text-white my-3 my-md-4" />
									<Link to="/projects/docbook" className="router-link">
										<h4 className="router-link-text">DocBook</h4>
									</Link>
									<Link to="/projects/bytecard" className="router-link">
										<h4 className="router-link-text">Byte Card</h4>
									</Link>
									<Link to="/projects/kitchenmate" className="router-link">
										<h4 className="router-link-text">Kitchen Mate</h4>
									</Link>
									<Link to="/projects/sync" className="router-link">
										<h4 className="router-link-text">LNMIIT Sync</h4>
									</Link>
									<Link to="/projects/yelpcamp" className="router-link">
										<h4 className="router-link-text">Yelp Camp</h4>
									</Link>
									<Link to="/projects/movietime" className="router-link">
										<h4 className="router-link-text">Movie Time!</h4>
									</Link>
								</div>
							</div>
						</div>
					</div>
					<div id="achievements" className="row align-items-center mx-0 content">
						<div className="col-md-6 text-center px-0">
							<div className="row justify-content-center mx-0">
								<div className="col-10 col-lg-8 px-0 blurb mb-5 mb-md-0">
									<h2>ACHIEVEMENTS</h2>
									<i className="fas fa-trophy fa-4x text-white my-3 my-md-4" />
									<p className="lead">
										I have successfully collaborated with my fellow developers to launch an
										<a
											className="inline-link"
											href="https://play.google.com/store/apps/details?id=com.infinitysolutions.lnmiitsync"
										>
											&nbsp;android app&nbsp;
										</a>
										for which I have worked as a backend developer.
									</p>
									<p className="lead">
										Won the
										<a className="inline-link" href="https://devpost.com/software/bytecard">
											&nbsp;most voted app&nbsp;
										</a>
										in Cybros Hackathon along with Github Backpacks.
									</p>
								</div>
							</div>
						</div>
						<div className="col-md-6 px-0">
							<img
								className="img-fluid"
								src="imgs/christopher-gower-m_HRfLhgABo-unsplash.jpg"
								alt="A laptop kept on the table with code editor open"
							/>
						</div>
					</div>
				</section>

				<nav className="navbar navbar-dark navbar-expand py-0 sticky-bottom justify-content-center mb-2">
					<ul className="navbar-nav">
						<li className="nav-item">
							<a href="mailto:nandisubhajit666@gmail.com" className="nav-link">
								<i className="fas fa-envelope fa-4x mx-3 mx-md-5" />
							</a>
						</li>
						<li className="nav-item">
							<a href="https://github.com/Kristency" className="nav-link">
								<i className="fab fa-github fa-4x mx-3 mx-md-5" />
							</a>
						</li>
						<li className="nav-item">
							<a href="https://www.linkedin.com/in/subhajit-nandi" className="nav-link">
								<i className="fab fa-linkedin fa-4x mx-3 mx-md-5" />
							</a>
						</li>
					</ul>
				</nav>
			</Fragment>
		)
	}
}

export default LandingPage
